<template>
  <div class="addMouduleSidebar">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="addTabModuleButton">
      <el-button style="border-radius: 20px" @click="dialogFormVisible = true"
        ></el-button
      >
      <!--  -->
    </div>

    <!--  -->
    <div class="addMouduleTabTableOut">
      <el-table :data="tableTabData" border style="width: 100%">
        <!-- el-table-column -->
        <el-table-column type="expand">
          <!--  type="expand"  Scoped slot el-table-column  Scoped slot  -->
          <template slot-scope="props">
            <!--  -->
            <!-- propstableTabData props.row -->
            <div class="addMouduleTabTableOut">
              <el-table
                class="addMouduleTabTable"
                :data="props.row.tableSidebarData"
                border
              >
                <el-table-column
                  prop="titleName"
                  label=""
                ></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button
                      @click="editModuleSiderbar(scope.$index, scope.row)"
                      ></el-button
                    >
                    <el-button
                      type="danger"
                      @click="deleteModuleSiderbar(scope.$index, scope.row)"
                      ></el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tabName"
        ></el-table-column>
      </el-table>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.titleName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.tableName" placeholder="">
              <!-- labelvalue -->
              <el-option
                v-for="(item, index) in tableTabData"
                :key="index"
                :label="item.tabName"
                :value="item.tabName"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button
            type="primary"
            @click="addModuleSiderbar(form.tableName, form.titleName)"
            > </el-button
          >
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "AddMouduleSidebar",
  components: {
    // HelloWorld
  },
  data() {
    return {
      // tableTabData: [
      // {
      //   // 
      //   tabName: "",
      //   tableSidebarData: [
      //     {
      //       sidebarName: "1",
      //     },
      //     {
      //       sidebarName: "2",
      //     },
      //     {
      //       sidebarName: "3",
      //     },
      //   ],
      // },
      //   {
      //     tabName: "",
      //     tableSidebarData: [
      //       {
      //         sidebarName: "1",
      //       },
      //       {
      //         sidebarName: "2",
      //       },
      //       {
      //         sidebarName: "3",
      //       },
      //     ],
      //   },
      // ],
      tableTabData: [],
      // 
      dialogFormVisible: false,
      form: {
        titleName: "", // 
        tableName: "", // 
      },
      formLabelWidth: "120px",
    };
  },
  mounted() {
    // 
    axios
      .post("/showTables")
      .then((response) => {
        console.log(response);
        console.log(response.object);
        // 
        this.tableTabData = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // 
    addModuleSiderbar(tableName, titleName) {
      this.dialogFormVisible = false; // 

      // 
      console.log(tableName); // 
      console.log(titleName); // 

      // 
      axios
        .post("/CreateTitle", {
          tableName: tableName,
          titleName: titleName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object);
        })
        .catch((error) => {
          console.log(error);
        });

      // 
      this.$router.replace({
        path: "/nullabc.vue",
        name: "Nullabc",
        params: {
          path: "/admin/addModuleSidebar",
          name: "AddModuleSidebar",
        },
      });
    },
    // 
    editModuleSiderbar(index, row) {
      console.log(index, row);

      //  row.titleName;
      console.log(row.titleName);
      // 
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: "",
      }).then(({ value }) => {
        console.log(value);
        //  -- axios
        axios
          .post("/changetitles", {
            titleName: row.titleName,
            titleNewName: value,
          })
          .then((response) => {
            console.log(response);
            console.log(response.object); // 

            // massage
            this.$message({
              type: "success",
              message: ": " + value,
            });
            // 
            this.$router.replace({
              path: "/nullabc.vue",
              name: "Nullabc",
              params: {
                path: "/admin/addModuleSidebar",
                name: "AddModuleSidebar",
              },
            });
          })
          .catch((error) => {
            console.log(error);

            // massageerror
            this.$message({
              type: "info",
              message: error,
            });
          });
      });
    },
    // 
    deleteModuleSiderbar(index, row) {
      console.log(index, row);
      console.log(row.titleName);
      axios
        .post("/deletetitle", {
          titleName: row.titleName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object); // 

          // 
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/admin/addModuleSidebar",
              name: "AddModuleSidebar",
            },
          });
        })
        .catch((error) => {
          console.log(error);

          // massageerror
          this.$message({
            type: "info",
            message: "" + row.sidebarName + "" + error,
          });
        });
    },
  },
};
</script>


<style>
.addTabModuleButton {
  float: right;
  padding: 15px;
  margin: 0 auto;
}
.addMouduleTabTableOut {
  margin: 10px;
}
.addMouduleTabTable {
  width: 50%;
  border-radius: 10px;
}
</style>