import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 
  state: {
    item: {},// /
    isLogin: false,// 
    isLoginWindows: false,// 
    isAdminPage: false,// 
    isEdit: false,// 
    sidebarLeftList: []// 
  },
  mutations: {
    //  state  $store.state msg 
    newItem(state, msg) {
      state.item = msg;
    },
    isLoginWindows(state) {
      state.isLoginWindows = !state.isLoginWindows;
    },
    isLogin(state) {
      state.isLogin = !state.isLogin;
    },
    isAdminPage(state) {
      state.isAdminPage = !state.isAdminPage;
    },
    isEdit(state, msg) {
      // state.isEdit = !state.isEdit;
      state.isEdit = msg;
    },
    newSidebarLeftList(state, msg) {
      state.sidebarLeftList = msg;

    }

  }
})

export default store