<template>
  <div class="addMouduleTab">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="addTabModuleButton">
      <el-button style="border-radius: 20px" @click="addModuleTab"
        ></el-button
      >
    </div>

    <!--  -->
    <div class="addMouduleTabTableOut">
      <el-table class="addMouduleTabTable" :data="tableData" border>
        <el-table-column prop="tabName" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button @click="editModuleTab(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              type="danger"
              @click="deleteModuleTab(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "AddMouduleTab",
  components: {
    // HelloWorld
  },
  data() {
    return {
      tableData: [],
    };
  },
  mounted() {
    // tableData,
    // 
    axios
      .post("/showTables")
      .then((response) => {
        console.log(response);
        console.log(response.object);
        // 
        this.tableData = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // 
    addModuleTab() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: "",
      })
        .then(({ value }) => {
          // --
          axios
            .post("/CreateTable", {
              tableName: value,
            })
            .then((response) => {
              console.log(response);
              console.log(response.object); // 

              if (response.code == 400) {
                this.$message({
                  type: "info",
                  message: value + " ",
                });
              } else {
                // massage
                this.$message({
                  type: "success",
                  message: ": " + value,
                });

                // 
                this.$router.replace({
                  path: "/nullabc.vue",
                  name: "Nullabc",
                  params: {
                    path: "/admin/addModuleTab",
                    name: "AddModuleTab",
                  },
                });
              }
            })
            .catch((error) => {
              console.log(error);

              // massageerror
              this.$message({
                type: "info",
                message: error,
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },

    // 
    editModuleTab(index, row) {
      console.log(index, row);

      //  row.tabName;

      // 
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: "",
      }).then(({ value }) => {
        //  -- axios
        axios
          .post("/changeTables", {
            tableName: row.tabName,
            tableNewName: value,
          })
          .then((response) => {
            console.log(response);
            console.log(response.object); // 

            // massage
            this.$message({
              type: "success",
              message: ": " + value,
            });

            // 
            this.$router.replace({
              path: "/nullabc.vue",
              name: "Nullabc",
              params: {
                path: "/admin/addModuleTab",
                name: "AddModuleTab",
              },
            });
          })
          .catch((error) => {
            console.log(error);

            // massageerror
            this.$message({
              type: "info",
              message: error,
            });
          });
      });
    },

    // 
    deleteModuleTab(index, row) {
      console.log(index, row);

      axios
        .post("/deleteTable", {
          tableName: row.tabName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object); // 
          // 
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/admin/addModuleTab",
              name: "AddModuleTab",
            },
          });
        })
        .catch((error) => {
          console.log(error);

          // massageerror
          this.$message({
            type: "info",
            message: "" + row.tabName + "" + error,
          });
        });
    },
  },
};
</script>


<style>
.addTabModuleButton {
  float: right;
  padding: 15px;
  margin: 0 auto;
}
.addMouduleTabTableOut {
  margin: 10px;
}
.addMouduleTabTable {
  width: 50%;
  border-radius: 10px;
}
</style>