<template>
  <div class="login">
    <!--  -->
    <div class="xtop">
      <span class="x" @click="XClose()">X</span>
    </div>
    <!--  -->
    <div class="form">
      <el-form :label-position="labelPosition" label-width="80px" :model="form">
        <el-form-item label="">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="loginButton" type="primary" @click="submitForm()"
            ></el-button
          >
          <!-- <el-button @click="resetForm('ruleForm')"></el-button> -->
        </el-form-item>
      </el-form>
    </div>
  </div>

  <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
  <!-- </div> -->
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "Login",
  components: {
    // HelloWorld
  },
  data() {
    return {
      // 
      labelPosition: "right",
      form: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    XClose() {
      // this.$emit("loginWindows");

      // 
      // console.log(this.$emit("loginWindowsTypeChange"));

      // 
      // this.$emit("loginWindows");
      this.$store.commit("isLoginWindows"); //  $store.commit  isLoginWindows
      console.log(this.$store.state.isLoginWindows);
    },

    submitForm() {
      axios
        .post("/login", {
          username: this.form.username,
          password: this.form.password,
        })
        // function(response)
        .then((response) => {
          console.log(response);
          console.log(response.data);

          if (response.code == 200) {
            console.log("");
            // 
            // this.$emit("loginResults");
            this.$store.commit("isLogin"); //  $store.commit  isLogin
            // (commit) mutations,
            console.log(this.$store.state.isLogin);

            // 
            // this.$emit("loginWindows");
            this.$store.commit("isLoginWindows"); //  $store.commit  isLoginWindows
            console.log(this.$store.state.isLoginWindows);

            // 
            this.$message({
              message: "",
              center: true,
            });

            // 
            this.$store.commit("isAdminPage"); //  $store.commit  isLoginWindows
            console.log(this.$store.state.isAdminPage);

            // admin
            this.$router.push("/admin");
          } else if (response.code == 400) {
            console.log("");
            // 
            // this.$emit("loginWindows");

            // 
            // this.$emit("loginWindows");
            this.$store.commit("isLoginWindows"); //  $store.commit  isLoginWindows
            console.log(this.$store.state.isLoginWindows);

            // 
            this.$message({
              message: "",
              center: true,
            });
          }
        })
        // function(error)
        .catch((error) => {
          console.log(error);
          console.log("");
          // 
          // this.$emit("loginWindows");
          // 
          // this.$emit("loginWindows");
          this.$store.commit("isLoginWindows"); //  $store.commit  isLoginWindows
          console.log(this.$store.state.isLoginWindows);

          // 
          this.$message({
            message: "",
            center: true,
          });
        });
    },
  },
};
</script>


<style>
.login {
  /* border: ghostwhite 2px solid; */
  background-color: RGB(241, 243, 244);
  position: absolute;

  width: 30%;
  top: 20%;
  left: 35%;
  z-index: 1500;

  /* display: none; */
}

.xtop {
  /* border: rgb(188, 174, 137) 1px solid; */

  background: rgb(223, 225, 226);
  width: 100%;
  height: 30px;
  margin-bottom: 30px;
  line-height: 30px; /* */
}
.x {
  float: right;
  padding: 0 4px;
  width: 5%;
  height: 30px;
  font-size: large;
  position: relative;
}
.x:hover {
  color: white;
  cursor: pointer;
  background: rgb(0, 153, 255);
}
.form {
  padding: 20px 30px;
}
/* .loginButton { */
/* margin: 0 auto; */
/* } */
</style>

